<template>
	<div id="home">
		<el-tag style="cursor:pointer;margin-top: 10px;margin-left: 10px;" :key="tag.label" :effect="tag.path===$route.path?'dark':'plain'" v-for="(tag,index) in tags"
			:closable="tag.label != '首页'"  :disable-transitions="false" @close="handleClose(tag,index)" @click="clicktag(tag.path)">
			{{tag.label}}
		</el-tag>
	</div>
</template>

<script>
	import {mapState,mapMutations} from 'vuex'
	export default {
		computed: {
			...mapState({
				tags(){
					return this.$store.state.top.tabsList
				}
			})
		},
		methods: {
			...mapMutations([
							'closeTab'		 
						]),
				      handleClose(tag,index) {
						  // console.log(tag.name,index)
						  //获取当前tags数组的长度
						  let tagsLength = this.tags.length -1
				        this.closeTab(tag);
						// 如果关闭的不是当前的路由 不进行跳转
						if(tag.name != this.$route.name)
						{
							
							return
						}
						
						//如果关闭的是最右边的路由 往左边跳
						if(index === tagsLength){
							this.$router.push(this.tags[index-1].path)
						}else{
							//否则往右边跳转
							this.$router.push(this.tags[index].path)
						}
						
				      },
			clicktag(index) {
				//判断路由是否重复点击，重复点击不生效
					this.$router.replace(index)
				
			}
		}
	}
</script>

