<template>
	<el-menu style="background-color: #304156; border: none; height: 100vh;" default-active="1-4-1"
		class="el-menu-vertical-demo"   :default-active="$route.path" router 
		:collapse="this.$store.state.top.isCollapse" text-color="#D6DEF0" active-text-color="#f0b43a"
		background-color="#304156">
		<el-menu-item v-for="(item,index) in nochildren" :index="item.path" :key="item.label" @click="labels(item)">
			<i :class="'el-icon-'+item.icon"></i>
			<span slot="title">{{item.label}}</span>
		</el-menu-item>

		<el-submenu index="1" v-for="(item,index) in childrens">
			<template slot="title">
				<i class="el-icon-location"></i>
				<span slot="title">{{item.label}}</span>
			</template>
			<el-menu-item-group>
				<el-menu-item v-for="(itens,index) in item.children" :index="itens.path" @click="labels2(itens)">
					<i :class="'el-icon-'+itens.icon"></i>
					<span slot="title">{{itens.label}}</span>
				</el-menu-item>
			</el-menu-item-group>
		</el-submenu>
	</el-menu>
</template>


<script>
	export default {
		computed: {
			menu(){
				return this.$store.state.top.menu
			},
			nochildren() {
				return this.menu.filter(iteam => !iteam.children)
			},
			childrens() {
				return this.menu.filter(iteam => iteam.children)
			},
			// storetop(){
			// 	return this.$store.state.top.labels
			// },
			
		
		},
		methods: {
			labels(itean){
				this.$store.commit('selectMenu',itean)
			},
			labels2(itean){
				this.$store.commit('selectMenu',itean)
			}
		},
		
	}
</script>

<style>
	.el-menu-vertical-demo:not(.el-menu--collapse) {
		width: 200px;
		min-height: 400px;
	}
</style>
